import React from "react";
import styled from "styled-components";
import { Heading, Text, BaseLayout } from "@pancakeswap-libs/uikit";
import useI18n from "hooks/useI18n";
import Page from "components/layout/Page";
import FarmStakingCard from "views/Home/components/FarmStakingCard";
// import LotteryCard from 'views/Home/components/LotteryCard'
import CakeStats from "views/Home/components/CakeStats";
import TotalValueLockedCard from "views/Home/components/TotalValueLockedCard";
import EarnAPYCard from "views/Home/components/EarnAPYCard";
// import EarnAssetCard from 'views/Home/components/EarnAssetCard'
// import WinCard from 'views/Home/components/WinCard'
// import Logo from 'views/Home/components/Logo'
import Landing from "components/custom/Landing";
import Popup from "components/Popup";

const Container = styled(Page)`
  background: #090e1a url("/hexBg.png");
  padding: 0;
  max-width: 100vw;
`;
const Hero = styled.div`
  align-items: center;

  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  margin-bottom: 32px;
  text-align: center;
`;

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 32px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 6;
    }
  }
`;

const CTACards = styled(BaseLayout)`
  align-items: start;
  margin-bottom: 32px;

  & > div {
    grid-column: span 6;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 4;
    }
  }
`;

const Flex = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  > div {
    width: 100%;
    margin: 1rem;
  }
  @media only screen and (min-width: 800px) {
    flex-direction: row;
    align-items: stretch;
    > div {
      min-width: 45%;
      max-width: 45%;
      margin: 0 1rem;
    }
  }
`;
const Home = ({ videoPlayed,setVideoPlayed }) => {
  const TranslateString = useI18n();

  return (
    <Container>
      {/* <Hero>
        <Text>{TranslateString(578, 'The #1 AMM and yield farm on Binance Smart Chain.')}</Text>
      </Hero> */}
      {/* <Popup/> */}
      <Landing videoPlayed={videoPlayed} setVideoPlayed={setVideoPlayed} />
      <div style={{ padding: "3rem 1rem" }}>
        <Flex>
          <EarnAPYCard />
          <FarmStakingCard />
          {/* <EarnAssetCard /> */}
          {/* <LotteryCard /> */}
        </Flex>
        <CTACards>
          {/* <EarnAPYCard /> */}
          {/* <WinCard /> */}
        </CTACards>
        <Flex>
          <CakeStats />
          <TotalValueLockedCard />
        </Flex>
      </div>
    </Container>
  );
};

export default Home;
